<template>
    <div>
        <div class="layer_popup" v-if="type === 'confirm' && customText" @touchmove.prevent>
            <div class="pop_cont_wrap">
                <div class="pop_cont">
                    <p class="sub_title" :class="customTextClass" v-html="customText"></p>
                </div>
                <div class="pop_footer">
                    <div class="btn_wrap">
                        <button type="button" class="btn btn_ok" :class="confirmBtnOption.leftBtn.class" :style="confirmBtnOption.leftBtn.style" @click="confirmLeftBtnClick" v-html="confirmBtnOption.leftBtn.text"></button>
                        <button type="button" class="btn btn_cancel" :class="confirmBtnOption.rightBtn.class" :style="confirmBtnOption.rightBtn.style"  @click="confirmRightBtnClick" v-html="confirmBtnOption.rightBtn.text">취소</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="layer_popup" v-if="type === 'alert' && customText" @touchmove.prevent>
            <div class="pop_cont_wrap">
                <div class="pop_cont">
                    <p class="sub_title" :class="customTextClass" v-html="customText"></p>
                </div>
                <div class="pop_footer">
                    <div class="btn_wrap">
                        <button type="button" class="btn btn_ok" @click="alertBtnClick" :class="alertBtnOption.class" :style="alertBtnOption.style" v-html="alertBtnOption.text"></button>
                    </div>
                </div>
            </div>
        </div>
        <transition name="slide-fade">
            <div class="toast_popup" v-if="toastMessage">
                <div class="toast_in">
                    <div class="txt">{{toastMessage}}</div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
        };
    },
    methods: {
    }
};
</script>

<style scoped>
.slide-fade-enter-active {
    transition: all .3s ease;
}
.slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
    transform: translateY(9rem);
    opacity: 0;
}
</style>



