<template>
    <div class="gray-bg">
    <div class="loginColumns animated fadeInDown">
        <div class="row">
            <div class="col-md-6">
                <h1 class="logo-name-register2">{{ Common.msg(multiLang.txt_input_msg_002) }}</h1>
                <img src="/ui/img/landing/member.png" alt="dashboard" class="img-responsive">
                <br>
                    <h3>{{ Common.msg(multiLang.tadadac_input_msg_003) }}</h3>
                    {{ Common.msg(multiLang.text_explain_01) }}
                    <br>
                    {{ Common.msg(multiLang.text_explain_02) }}
                    <b style="color:black">chksong@boneis.com</b>
            </div>
            <div class="col-md-6">
                <div class="ibox-content">
                    <div class="lang-btn-div">
                        <button v-for="(value, key) in langs" :key="key" class="btn btn-outline btn-primary" @click = transLang(value)>
                            {{key}}
                        </button>
                    </div>
                    <br>
                    <div class="radio-group">
                        <div class="radio-div"><label><input type="radio" name="user" value="0" v-model="submitData.userTypeCode"> {{ Common.msg(multiLang.USER_TYPE_01) }} </label></div>
                        <div class="radio-div"><label><input type="radio" name="user" value="1" v-model="submitData.userTypeCode"> {{ Common.msg(multiLang.USER_TYPE_02) }} </label></div>
                    </div>
                    <p>
                        <small v-if="errorCheck === 'empty'" class="errorCheck">
                            <i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ Common.msg(multiLang.txt_input_msg_009) }}
                        </small>
                    </p>
                    <form class="m-t" role="form" @submit.prevent="checkValues">
                        <div class="form-group" style="text-align:left;">
                            <label class="required" style="margin-left:16px;">{{ Common.msg(multiLang.TEXT_ID) }}</label>
                            <input type="text" class="form-control" :placeholder="Common.msg(multiLang.TEXT_ID)" ref="mbId" v-model="submitData.mbId" maxlength="30">
                            <small v-if="errorCheck === 'mbId'" class="errorCheck" ><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ Common.msg(multiLang.txt_input_msg_005) }} </small>
                            <small v-if="errorCheck === 'duplicateId'" class="errorCheck" ><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ Common.msg(multiLang.txt_input_msg_007) }}</small>
                        </div>
                        <div class="form-group" style="text-align:left;">
                            <label class="required" style="margin-left:16px;">{{ Common.msg(multiLang.TEXT_PASSWORD) }}</label>
                            <input type="password" class="form-control" :placeholder="Common.msg(multiLang.TEXT_PASSWORD)" ref="mbPassword" v-model="submitData.mbPassword">
                        </div>
                        <div class="form-group" style="text-align:left;">
                            <label class="required" style="margin-left:16px;">{{ Common.msg(multiLang.TEXT_PASSWORD) }} {{ Common.msg(multiLang.TEXT_CONFIRM) }}</label>
                            <input type="password" class="form-control" :placeholder="Common.msg(multiLang.TEXT_PASSWORD) + space + Common.msg(multiLang.TEXT_CONFIRM)" ref="confirmPw" v-model="submitData.confirmPw">
                            <small v-if="errorCheck === 'confirmPw'"  class="errorCheck"><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ Common.msg(multiLang.txt_input_msg_006) }} </small>
                        </div>
                        <div class="form-group" style="text-align:left;" v-if="submitData.userTypeCode === '1'">
                            <label class="required" style="margin-left:16px;">{{ Common.msg(multiLang.TEXT_MASTER_KEY) }}</label>
                            <input type="text" class="form-control" :placeholder="Common.msg(multiLang.TEXT_MASTER_KEY)" ref="comMasterKey" v-model="submitData.comMasterKey" maxlength="50">
                            <small v-if="errorCheck === 'comMasterKey'"  class="errorCheck"><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ Common.msg(multiLang.txt_input_msg_008) }} </small>
                        </div>
                        <div class="form-group" style="text-align:left;">
                            <label class="required" style="margin-left:16px;">{{ Common.msg(multiLang.TEXT_EMAIL) }}</label>
                            <input type="text" class="form-control" :placeholder="Common.msg(multiLang.TEXT_EMAIL)" ref="mbEmail" v-model="submitData.mbEmail" maxlength="30">
                            <small v-if="errorCheck === 'mbEmail'"  class="errorCheck"><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ Common.msg(multiLang.txt_input_msg_017) }} </small>
                        </div>
                        <div class="form-group" style="text-align:left;">
                            <label class="required" style="margin-left:16px;">{{ Common.msg(multiLang.TEXT_HP) }}</label>
                            <input type="text" class="form-control" :placeholder="Common.msg(multiLang.TEXT_HP)" ref="mbHp" v-model="submitData.mbHp" maxlength="30">
                            <small v-if="errorCheck === 'mbHp'"  class="errorCheck"><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> {{ Common.msg(multiLang.txt_input_msg_018) }} </small>
                        </div>
                        <!--<div class="form-group">
                            <div class="checkbox i-checks"><label> <input type="checkbox"><i></i> Agree the terms and policy </label></div>
                        </div>-->
                        <button type="submit" class="btn btn-primary block full-width m-b">{{ Common.msg(multiLang.TEXT_REGISTER) }}</button>

                        <p class="text-muted text-center"><small>{{ Common.msg(multiLang.txt_input_msg_004) }}</small></p>
                        <a class="btn btn-sm btn-white btn-block" href="/member/login">{{ Common.msg(multiLang.TEXT_LOGIN) }}</a>
                    </form>
                    <p class="m-t"> <small><strong>Copyright</strong> Boneis Company &copy; 2011-2018</small> </p>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
import { mapActions, mapState } from 'vuex';
import au from '../../common/js/AjaxUtils.js';
import validation from './validation.js';

export default {
    name: 'Register',
    computed: {
        ...mapState('commonStore', [
            'multiLang'
        ])
    },
    data() {
        return {
            submitData: {
                mbId: '',
                mbPassword: '',
                confirmPw: '',
                comMasterKey: '',
                mbEmail: '',
                mbHp: '',
                userTypeCode: '0'
            },

            errorMsg: {},
            space: ' ',
            langs: { Korea: 'KOR', English: 'ENG', Indonesia: 'IND'},
            errorCheck: '' // 유효성 검사
        };
    },
    methods: {
        ...mapActions('commonStore', [
            'getPatchMultiLangList'
        ]),

        checkValues() {
            this.errorCheck = '';
            validation.check(this.submitData).then((result) => {
                if (result.check) {
                    this.sendPost(); // validation 후 이상없으면 회원가입 POST 호출
                } else {
                    this.errorCheck = result.value; // 이상있으면 메세지 노출
                }
            }).catch(function (err) {
                this.layerPopup.alert(this.Common.msg(this.multiLang.TEXT_FAIL)); // 실패하였습니다.
                return false;
            });
        },

        sendPost: function () { /* 회원가입 POST 호출 */
            let that = this;
            au.post('/api/member/register', this.submitData).then((res) => {
                if(res.data.result === 'OK') {
                    this.layerPopup.alert(that.Common.msg(that.multiLang.txt_input_msg_010), function () { window.location.href = '/member/login'; }); // 성공적으로 등록되었습니다.
                }else {
                    this.layerPopup.alert(that.Common.msg(that.multiLang.txt_input_msg_011)); // 등록에 실패하였습니다.
                }
            }).catch(function(error) {
                this.layerPopup.alert(that.Common.msg(that.multiLang.txt_input_msg_011)); // 등록에 실패하였습니다.
            });
        },

        transLang: function (lang) {
            window.location.href = `/member/register?lang=${lang}`;
        }
    },
    created() {
        this.getPatchMultiLangList();
    },
    mounted() {

    }
};

</script>
<style scoped>

</style>



