import Vue from 'vue';
import extend from 'object-extend';
import LayerPopup from './LayerPopup.vue';
import multiLang from '../../store/commonStore.js';

const LayerPopupConstructor = Vue.extend(LayerPopup);
let EventBus = new Vue();

export default {
    install: () => {
        const defaultData = {
            customText: '',
            customTextClass: '',
            type: 'alert',
            confirmBtnOption: {
                leftBtn: {
                    text: '확인',
                    style: {},
                    class: ''
                },
                rightBtn: {
                    text: '취소',
                    style: {},
                    class: ''
                },
                callback: null
            },
            alertBtnOption: {
                text: '확인',
                style: {},
                class: '',
                callback: null
            },
            toastTimer: 2500,
            toastMessage: '',
            showToast: false
        };

        let LayerPopupInstance = new LayerPopupConstructor({
            data: JSON.parse(JSON.stringify(defaultData)),
            methods: {
                confirmLeftBtnClick: () => {
                    let option = LayerPopupInstance.confirmBtnOption;

                    if(option && typeof option === 'object' && option.callback) {
                        option.callback(0, option.leftBtn.text);
                    }

                    EventBus.$emit('ConfirmLeftBtnClick');
                },
                confirmRightBtnClick: () => {
                    let option = LayerPopupInstance.confirmBtnOption;

                    if(option && typeof option === 'object' && option.callback) {
                        option.callback(1, option.rightBtn.text);
                    }

                    EventBus.$emit('ConfirmRightBtnClick');
                },
                alertBtnClick: () => {
                    let option = LayerPopupInstance.alertBtnOption;

                    if(option && typeof option === 'object' && option.callback) {
                        option.callback();
                        extend(LayerPopupInstance, defaultData);
                    } else {
                        extend(LayerPopupInstance, defaultData);
                    }

                    EventBus.$emit('AlertBtnClick');
                }
            }
        });

        let setDefault = () => {
            defaultData.confirmBtnOption.leftBtn.text = multiLang.state.multiLang.TEXT_CONFIRM;
            defaultData.confirmBtnOption.rightBtn.text = multiLang.state.multiLang.Cancel;
            defaultData.alertBtnOption.text = multiLang.state.multiLang.TEXT_CONFIRM;
            extend(LayerPopupInstance, JSON.parse(JSON.stringify(defaultData)));
        };

        let toastTimeout = null;
        Vue.prototype.layerPopup = {
            alert: (text, option, textClass) => {
                setDefault();

                LayerPopupInstance.customText = text;
                if(textClass) {
                    LayerPopupInstance.customTextClass = textClass;
                }

                if(option && typeof option === 'object') {
                    extend(LayerPopupInstance.alertBtnOption, option);
                } else if(option && typeof option === 'function') {
                    LayerPopupInstance.alertBtnOption.callback = option;
                }
            },
            confirm: (text, option, textClass) => {
                setDefault();

                LayerPopupInstance.type = 'confirm';
                LayerPopupInstance.customText = text;
                if(textClass) {
                    LayerPopupInstance.customTextClass = textClass;
                }

                if(option && typeof option === 'object') {
                    extend(LayerPopupInstance.confirmBtnOption, option);
                } else if(option && typeof option === 'function') {
                    LayerPopupInstance.confirmBtnOption.callback = option;
                }
            },
            toast: (text, timer) => {
                setDefault();

                LayerPopupInstance.type = 'toast';

                if(timer) {
                    LayerPopupInstance.toastTimer = timer;
                }

                if(text) {
                    LayerPopupInstance.toastMessage = text;

                    if(toastTimeout) {
                        window.clearTimeout(toastTimeout);
                    }

                    toastTimeout = window.setTimeout(() => {
                        LayerPopupInstance.toastMessage = '';
                    }, LayerPopupInstance.toastTimer);
                }
            },
            hide: () => {
                setDefault();
            },
            event: EventBus
        };

        LayerPopupInstance.vm = LayerPopupInstance.$mount();
        LayerPopupInstance.dom = LayerPopupInstance.vm.$el;
        document.body.appendChild(LayerPopupInstance.dom);
    }
};



