import Vue from 'vue';

export default {
    install: () => {
        Vue.prototype.Common = {
            msg: function(msg, options) {
                if(msg && options) {
                    for (let i = 0; i < options.length; i++) {
                        let opt = options[i];
                        msg = msg.replace(`{${i}}`, opt);
                    }
                }
                return msg;
            }
        };
    }
};



