import axios from 'axios';

const commonStore = {
    namespaced: true,
    state: {
        multiLang: {},
        commonCode: [],
        commonAccountList: [],
        commonAccountMap: []
    },
    mutations: {
        updateMultiLangList (state, payload) {
            state.multiLang = payload.multiLang;
        },
        updateCommonCode (state, payload) {
            state.commonCode = payload.commonCode;
        },
        updateCommonAccount (state, payload) {
            state.commonAccountList = payload.commonAccountList;
        },
        updateCommonAccountMap (state, payload) {
            state.commonAccountMap = payload.commonAccountMap;
        }
    },
    actions: {
        getPatchMultiLangList ({ commit, state }, payload) {
            if(!state.multiLang.LAST_TIME) {
                return axios.get('/api/language/multiLang').then((response) => {
                    let data = response.data;

                    if (data) {
                        commit('updateMultiLangList', {
                            multiLang: data
                        });
                    }
                }).catch((error) => {

                });
            }
        },
        getPatchCommonCode ({ commit, state }, payload) {
            if(!state.commonCode.LAST_TIME) {
                return axios.get('/api/common/largeCommonCode').then((response) => {
                    let data = response.data;
                    if (data.result === 'OK') {
                        commit('updateCommonCode', {
                            commonCode: data.commonCodeMap
                        });
                    }
                }).catch((error) => {

                });
            }
        },
        getPatchCommonAccountMap({ commit, state }, payload) { // 구분 코드 값을 키값으로 데이터를 가져옴
            if(!state.commonAccountMap.LAST_TIME) {
                return axios.get('/api/common/getCommonAccountMap').then((response) => {
                    let data = response.data;
                    if (data.result === 'OK') {
                        commit('updateCommonAccountMap', {
                            commonAccountMap: data.commonAccountMap
                        });
                    }
                }).catch((error) => {

                });
            }
        },
        getPatchCommonAccountList({ commit, state }, payload) { // 구분 코드 이름 , 구분 코드,멀티랭 가져옴
            if(!state.commonAccountList.LAST_TIME) {
                return axios.get('/api/common/getCommonAccountList').then((response) => {
                    let data = response.data;
                    if (data.result === 'OK') {
                        commit('updateCommonAccount', {
                            commonAccountList: data.commonAccountList
                        });
                    }
                }).catch((error) => {

                });
            }
        },
        creanMultiLangList ({ commit, state }, payload) {
            commit('updateMultiLangList', {
                multiLang: {}
            });
        }
    }
};

export default commonStore;



