import Vue from 'vue';
import extend from 'object-extend';
import LoadingLayer from './LoadingLayer.vue';


const LoadingLayerConstructor = Vue.extend(LoadingLayer);
let EventBus = new Vue();

export default {
    install: () => {
        let defaultData = {
            isShow: false
        };

        let LoadingLayerInstance = new LoadingLayerConstructor({
            data: Object.assign({}, defaultData),
            methods: {
            }
        });

        let showCount = 0;
        Vue.prototype.LoadingLayer = {
            show: () => {
                showCount++;
                LoadingLayerInstance.isShow = true;
            },
            hide: () => {
                showCount--;
                if(!showCount) {
                    LoadingLayerInstance.isShow = false;
                }
            },
            toggle: () => {
                LoadingLayerInstance.isShow = !LoadingLayerInstance.isShow;
            },
            event: EventBus
        };

        window.LoadingLayer = Vue.prototype.LoadingLayer;

        LoadingLayerInstance.vm = LoadingLayerInstance.$mount();
        LoadingLayerInstance.dom = LoadingLayerInstance.vm.$el;
        document.body.appendChild(LoadingLayerInstance.dom);
    }
};



