import au from '../../common/js/AjaxUtils.js';

function initialState() {
    return {
        insertEmpData: {
            comMasterKey: '',
            empNo: '',
            empNm: '', // 불러올거
            idNum: '',
            sex: '', // 불러올거
            stayContry: '',
            division: '',
            staffLevel: '',
            bgStaffDt: null,
            resignYn: false,
            endStaffDt: null,
            position: '',
            address: '', // 불러올거
            fmCount: '0',
            childCount: '0',
            empEmail: '', // 불러올거
            empClassify: '',
            empHp: '', // 불러올거
            marryYn: '',
            basicSalaryAmt: '',
            resignReason: '',
            addressDetail: '',
            uploadFile: null,
            upFilePath: '', // 불러올거?
            upFileName: '',
            upFileFlag: '',
            empMbId: '', // 불러올거?
            insertType: 'INSERT',
            idUploadFile: null,
            idUpFilePath: '',
            idUpFileName: '',
            idUpFileFlag: ''
        }
    };
}

const empStore = {
    namespaced: true,
    state: initialState,
    mutations: {
        SET_EMP_DATA: function(state, {response, empMbId}) {
            state.insertEmpData.empMbId = empMbId;

            if(response.resumeData) {
                Object.assign(state.insertEmpData, {
                    empNm: response.resumeData.resName,
                    sex: response.resumeData.gender,
                    empEmail: response.resumeData.email,
                    empHp: response.resumeData.phoneNumber,
                    address: response.resumeData.address
                });
            }
        },
        INIT_EMP_DATA: function(state) {
            // state 초기화하기
            const s = initialState();
            Object.keys(s).forEach(function(key) { state[key] = s[key]; });
        }
    },
    actions: {
        insertResumeToEmp({commit, state}, empMbId) {
            au.post('/api/myPage/getResumeData', {receiveId: empMbId}).then((res) => {
                if (res.data.result === 'OK') {
                    commit('SET_EMP_DATA', { response: res.data, empMbId});
                    // 여기에서 setEmpData
                } else {
                    alert('error1');
                }
            }).catch((error) => {
                alert('error2');
            });
        }
    }
};

export default empStore;



