import au from '../../common/js/AjaxUtils.js';

const validation = {
    mbId: /^[a-z0-9]{4,12}$/, // 아이디
    mbEmail: /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/, // 이메일
    mbHp: /^[0-9]+$/, // 전화번호

    result: {
        check: true,
        value: ''
    },
    async check(submitData) {
        let checkResult = await this.checkPost(submitData.mbId, submitData.comMasterKey, submitData.userTypeCode);
        if(submitData.mbId === '' || submitData.mbPassword === '' || submitData.confirmPw === ''
            || (submitData.userTypeCode === '1' && submitData.comMasterKey === '')
            || submitData.mbEmail === '' || submitData.mbHp === '') {
            this.result.check = false;
            this.result.value = 'empty';
        }else if(!this.mbId.test(submitData.mbId)) {
            this.result.check = false;
            this.result.value = 'mbId';
        }else if(checkResult === 'ERROR_ID') {
            this.result.check = false;
            this.result.value = 'duplicateId';
        } else if(submitData.mbPassword !== submitData.confirmPw) {
            this.result.check = false;
            this.result.value = 'confirmPw';
        } else if(checkResult === 'ERROR_KEY') {
            this.result.check = false;
            this.result.value = 'comMasterKey';
        } else if(!this.mbEmail.test(submitData.mbEmail)) {
            this.result.check = false;
            this.result.value = 'mbEmail';
        } else if(!this.mbHp.test(submitData.mbHp)) {
            this.result.check = false;
            this.result.value = 'mbHp';
        } else {
            this.result.check = true;
            this.result.value = '';
        }
        return this.result;
    },
    checkPost(mbId, comMasterKey, userTypeCode) {
        let result = au.post('/api/member/checkValues', {
            mbId: mbId,
            comMasterKey: comMasterKey,
            userTypeCode: userTypeCode
        }).then(res => res.data.result).catch(function (err) {
            return err;
        });
        return result;
    }
};


export default validation;



