import axios from 'axios';
import qs from 'qs';

axios.interceptors.request.use(function(result) {
    if(window.LoadingLayer) {
        window.LoadingLayer.show();
    }
    return result;
}, function(error) {
    if(window.LoadingLayer) {
        window.LoadingLayer.hide();
    }
    return Promise.reject(error);
});

axios.interceptors.response.use(function(result) {
    if(window.LoadingLayer) {
        window.LoadingLayer.hide();
    }
    return result;
}, function(error) {
    if(window.LoadingLayer) {
        window.LoadingLayer.hide();
    }
    return Promise.reject(error);
});

const AjaxUtil = {
    post(url, params) {
        let options = {
            method: 'POST',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data: params ? qs.stringify(params) : '',
            url
        };

        return axios(options);
    },
    get(url, params) {
        return axios.get(url, {
            params: params
        });
    }
};

export default AjaxUtil;



