import Vue from 'vue';
import Vuex from 'vuex';
import commonStore from './commonStore';
import empStore from '../components/empManage/empStore';

Vue.use(Vuex);

export default new Vuex.Store({
    modules: {
        commonStore: commonStore,
        empStore: empStore
    }
});



