import Vue from 'vue';
import VueScrollingTable from 'vue-scrolling-table';
import VCalendar from 'v-calendar';
import VueCookie from 'vue-cookie';
import VueFormWizard from 'vue-form-wizard';
import Croppa from 'vue-croppa';
import gallery from 'img-vuer';
import Tabs from 'vue-tabs-component';
import store from '../../store/Store';
import commonUtil from '../plugin/CommonUtil';
import LayerPopup from '../plugin/LayerPopup.js';
import LoadingLayer from '../plugin/LoadingLayer.js';
import 'v-calendar/lib/v-calendar.min.css';
import 'vue-croppa/dist/vue-croppa.css';
import 'vue-tabs-component/docs/resources/tabs-component.css';
import 'vue-form-wizard/dist/vue-form-wizard.min.css';

const datepickerOptions = {};
let eventBus = new Vue();
Vue.prototype.$event = eventBus;
Vue.use(commonUtil);
Vue.use(LayerPopup);
Vue.use(LoadingLayer);
Vue.use(VCalendar, {
    locale: (VueCookie.get('LOCALE') !== 'in-IN' ? VueCookie.get('LOCALE') : 'id-ID')
});
Vue.use(Croppa);
Vue.use(Tabs);
Vue.use(VueFormWizard);
Vue.use(gallery, {
    swipeThreshold: 150
});
// Vue.use(VueScrollingTable);
export {Vue, store};



